.login-button {
    float: left;
    margin-right: 15px;
    width: @width37;
    INPUT {
        -webkit-appearance: none;
        background: @color3;
        border-radius: 5px;
        border: none;
        color: @color0;
        display: block;
        padding: 5px 10px 5px 10px;
        text-align: center;
        width: @width37;
        &:hover {
            background: @color0;
            border: 2px @color2 solid;
            color: @color2;
            padding: 3px 8px 3px 8px;
        }
    }
}
.search-button {
    background-image: url(img/images/icons@2x.png);
    background-position: 1px -49px;
    background-size: 30px auto;
    cursor: pointer;
    display: block;
    height: @height18;
    position: absolute;
    right: 50px;
    width: @width38;
    z-index: 2;
    &:hover {
        background-position: 1px -82px;
    }
}
.search-input-button {
    position: absolute;
    right: 50px;
    top:10px;
    INPUT {
        -webkit-appearance: none;
        background-color:@color18;
        background-image: url(img/images/icons@2x.png);
        background-position: 1px -49px;
        background-size: 30px auto;
        border: none;
        cursor: pointer;
        cursor: pointer;
        display: block;
        height: @height18;
        outline:none;
        width: @width38;
        &:hover {
            -webkit-appearance: none;
            background-position: 1px -82px;
        }
    }
}
A.sidebar-link-button {
    background: @color6;
    border-radius: 5px;
    color: @color0;
    font-size: .8em;
    padding: 3px 10px 3px 10px;
    width: auto;
    &:hover {
        background:@color3;
    }
}